<template>
  <div class="export">
    <el-card class="box-card">
      <div slot="header"
           class="clearfix">
        <el-row>
          <el-col :span="16">
            <div class="grid-content left"></div>
          </el-col>
          <el-col :span="8">
            <div class="grid-content right"></div>
          </el-col>
        </el-row>
      </div>
      <div v-for="o in pageList"
           :key="o"
           class="text item">
        <el-row>
          <el-col :span="20">
            <div class="grid-content left">{{o.name}}</div>
          </el-col>
          <el-col :span="4">
            <div class="grid-content right">{{o.score}}</div>
          </el-col>
        </el-row>
      </div>
      <el-pagination small
                     layout="prev, pager, next"
                     :total="exportData.length"
                     :page-size="pageSize"
                     @current-change="changePage">
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      exportData: [{
        name: '1',
        score: '92'
      }, {
        name: '2',
        score: '89'
      }, {
        name: '3',
        score: '86'
      }, {
        name: '4',
        score: '78'
      }, {
        name: '5',
        score: '78'
      }, {
        name: '6',
        score: '100'
      }, {
        name: '7',
        score: '80'
      }, {
        name: '8',
        score: '92'
      }, {
        name: '9',
        score: '89'
      }, {
        name: '10',
        score: '89'
      }],
      pageSize: 5,
      pageList: [] // 
    }
  },
  methods: {
    changePage: function (currentPage) {
      for (var i = 0; i < this.pageSize; i++) {
        if ((i + this.pageSize * (currentPage - 1)) < this.exportData.length) {
          this.pageList.splice(i, 1, this.exportData[i + this.pageSize * (currentPage - 1)])
        } else {
          this.pageList.splice(i, 1, '')
        }
      }
    }
  },
  created () {
    for (var i = 0; i < 5; i++) {
      this.pageList[i] = this.exportData[i]
    }
  }
}
</script>

<style>
.text {
  font-size: 14px;
}

.item {
  padding: 5px 0;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}

.clearfix:after {
  clear: both
}
.export{
  width: 30%;
}
.box-card {
  width: 100%;
}
@media screen and (max-width:700px){
  .export{
    width:100%;
    position: absolute;
    top: 0;
  }
} 
</style>



