<template>
  <div>
    <el-collapse-transition>
      <div class="transition-box" v-if="showCard">
        <el-card class="checkbox-bar">
          <el-checkbox-group v-model="Grids" size="small" class="girdgroup" v-on:change="changeGrids">
            <el-checkbox v-for="item in GridDisplay" :key="item.name" :label="item.name" style="margin-left:10px"></el-checkbox>
          </el-checkbox-group>
          <el-select v-model="map_value" placeholder="" class="single-selector">
            <el-option
              v-for="item in maps"
              :key="item"
              :label="item"
              :value="item">
            </el-option>
          </el-select>
        </el-card>
      </div>
    </el-collapse-transition>
  </div>
</template>

<script>
export default {
  name:'setting',
  props:['showCard'],
  data () {
    return {
      Grids:[],
      GridDisplay:[{
        name:"",
        value:false
      },{
        name:"",
        value:false
      },{
        name:"1000M",
        value:false
      },{
        name:"1000M",
        value:false
      }],
      map_value:'',
      maps:["","","","",""]
    }
  },
  methods: {
    changeGrids(){
      this.$emit('changeGrids', this.Grids)
    }
  },
  watch:{
    map_value:function(){
      this.$emit('changeMap', this.map_value)
    }
  }
}
</script>

<style lang="scss" scoped>
  .transition-box {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    font-size:0.8rem;
  }
  .checkbox-bar{
    // display: flex;
    // flex-direction:column;
    // flex-wrap:wrap;
    // align-items: flex-start;
    .el-checkbox-group{
      display: flex;
      flex-direction:column;
      flex-wrap:wrap;
      align-items: flex-start;
    }
    .single-selector{
      margin-top:5%;
      width:80%;
    }
  }
  .el-card__header{
    padding:0 10% !important;
  }
  @media screen and (max-width:200px){
    .transition-box{
      width:100%;
    }
  }
</style>



