<template>
  <div>
    <el-collapse-transition>
      <div v-show="showCard" class="cardBar">
        <el-card>
          <el-form ref="form" :model="form" label-width="80px">
            <el-form-item label="">
              <el-select v-model="form.city"
                         filterable
                         placeholder="">
                <el-option v-for="item in form.citys"
                           :key="item"
                           :label="item"
                           :value="item">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-radio-group v-model="form.ditchType">
                <el-radio
                  v-for="item in form.ditches"
                  :key="item"
                  class="radio"
                  v-bind:label="item">{{item}}
                </el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="">
              <el-radio-group v-model="form.purpose">
                <el-radio
                  v-for="item in form.purposes"
                  :key="item"
                  class="radio"
                  v-bind:label="item">{{item}}
                </el-radio>
              </el-radio-group>
            </el-form-item>
<!--             <el-form-item>
              <el-button type="primary" @click="onSubmit" class="submit"></el-button>
            </el-form-item> -->
          </el-form>
          <el-table
            :data="excelData"
            stripe
            class="elTable">
            <el-table-column
              prop="ID"
              label="ID">
            </el-table-column>
            <el-table-column
              prop=""
              label="">
            </el-table-column>
            <el-table-column
              prop=""
              label="">
            </el-table-column>
            <el-table-column
              prop=""
              label="">
            </el-table-column>
            <el-table-column
              prop=""
              label="">
            </el-table-column>
            <el-table-column
              prop=""
              label="">
            </el-table-column>
            <el-table-column
              prop=""
              label="">
            </el-table-column>
          </el-table>
          <el-button class="select" justify="center">
            
            <input type="file" id="excel-file" v-on:change="readJSON">
          </el-button>
          <el-button type="primary" @click="onSubmit" class="select"></el-button>
        </el-card>
      </div>
    </el-collapse-transition>
  </div>
</template>

<script>
import XLSX from "xlsx"
import $ from "jquery"
var excelData =[]
export default {
  name: 'excelSelect',
  props:['showCard'],
  data () {
    return {
      excelData,
      show3: true,
      city: '',
      form: {
        city: '',
        ditchType: '',
        purpose: '',
        citys: ['','','','',''],
        ditches:['','',''],
        purposes: ['','','','CBD','','']
      }
    }
  },
  methods: {
    readJSON(e) {
      this.excelData = []
      var files = e.target.files

      var fileReader = new FileReader()
      fileReader.onload = (ev) => {
          try {
              var data = ev.target.result,
                  workbook = XLSX.read(data, {
                      type: 'binary'
                  })

          } catch (e) {
              console.log(e)
              console.log('')
              return
          }

          // 
          var fromTo = ''
          // 
          for (var sheet in workbook.Sheets) {
              if (workbook.Sheets.hasOwnProperty(sheet)) {
                  fromTo = workbook.Sheets[sheet]['!ref']
                  console.log(fromTo)
                  // var temp = XLSX.utils.sheet_to_json(workbook.Sheets[sheet]);
                  this.excelData = this.excelData.concat(XLSX.utils.sheet_to_json(workbook.Sheets[sheet]))
                  this.excelData.forEach((data, index)=>{
                    data.ID = index
                  })
                  // break; // 
              }
          }
      };

      // 
      fileReader.readAsBinaryString(files[0]);
    },
    onSubmit () {
      console.log(this.form)
    }
  },
  watch:{
    showCard:function() {
      this.excelData=[]
    }
  }
}
</script>

<style lang="scss" scoped>
  .cardBar{
    color:white;
    overflow: scroll;
    position:absolute;
    width:80%;
    left: 50%;
    max-height:80%;
    min-width:200px;
    top: 50%;
    transform: translate(-50%, -50%);
    .elTable{
      width:100%;
      margin-bottom:3%
    }
    .cell{
      font-size: 12px !important;
    }
    .mask{
      position:absolute;
      top:0;
      left:0;
      height:100%;
      width:100%;
      background-color:black;
      // z-index:0;
    }
    .radio{
      margin-left:10px;
    }
  }
  .select{
    position:relative;
    left:50%;
    display: block;
    margin:10px;
    transform:translate(-50%,0);
    input[type=file]{
      position:absolute;
      left:8%;
      top:5%;
      width:130%;
      opacity: 0;
      filter: alpha(opacity=0);
    }
  }

</style>



