<template>
  <el-collapse-transition>
    <el-card class="box-card" v-show="show">
      <div slot="header">
        <el-row>
          <el-col :span="20">
            <div class="grid-content left text"></div>
          </el-col>
          <el-col :span="4">
            <div class="grid-content right text">86</div>
          </el-col>
        </el-row>
      </div>
      <div v-for="o in detailData"
           :key="o"
           class="text item">
        <el-row>
          <el-col :span="20">
            <div class="grid-content left">{{o.name}}</div>
          </el-col>
          <el-col :span="4">
            <div class="grid-content right">{{o.score}}</div>
          </el-col>
        </el-row>
      </div>
      <el-button v-on:click="detailClose"></el-button>
    </el-card>
  </el-collapse-transition>
</template>

<script>
import Popper from "popper.js"
import $ from "jquery"

export default {
  data () {
    return {
      detailData: [{
        name: '',
        number: '2'
      }, {
        name: '',
        number: '1'
      }, {
        name: '',
        number: '5'
      }, {
        name: '',
        number: '11'
      }, {
        name: '',
        number: '4'
      }, {
        name: '',
        number: '2568'
      }, {
        name: '',
        number: '6533'
      }],
      location:'',
      score:'',
      show:false
    }
  },
  methods: {
    detailClose:function() {
      this.$emit('close')
    }
  },
  mounted: function() {
    this.$on('detailShow', ()=>{
      console.log('dsa')
      this.show = !this.show
    })
  }
}
</script>

<style scoped>
.text {
  font-size: 0.5rem;
}
.box-card {
  width: 100%;
}
</style>



