<template>
  <div>
    <el-collapse-transition>
      <div v-if="showCard" class="transition-box">
        <el-card >
          <el-form ref="form"
                   :model="form"
                   label-width="80px">
            <el-form-item label="">
              <el-select v-model="form.city"
                         filterable
                         placeholder="">
                <el-option v-for="item in form.citys"
                           :key="item"
                           :label="item"
                           :value="item">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-radio-group v-model="form.ditchType">
                <el-radio
                  v-for="item in form.ditches"
                  :key="item"
                  class="radio"
                  v-bind:label="item">{{item}}
                </el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="">
              <el-radio-group v-model="form.purpose">
                <el-radio
                  v-for="item in form.purposes"
                  :key="item"
                  class="radio"
                  v-bind:label="item">{{item}}
                </el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="onSubmit" class="submit"></el-button>
            </el-form-item>
          </el-form>
        </el-card>
      </div>
    </el-collapse-transition>
  </div>
</template>

<script>
export default {
  name:'labelLoc',
  props:['showCard'],
  data: () => ({
    show3: true,
    city: '',
    form: {
      city: '',
      ditchType: '',
      purpose: '',
      citys: ['','','','',''],
      ditches:['','',''],
      purposes: ['','','','CBD','','']
    }
  }),
  methods: {
    onSubmit () {
      console.log(this.form)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .el-radio {
    width: 50px;
    margin: 5px;
  }
  .transition-box {
    width:35%;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
  }
  el-radio-group{
    display: flex;
    flex-direction:row;
    flex-wrap:wrap;
  }
  @media screen and (max-width:800px){
    .transition-box{
      width:70%;
    }
  }
  @media screen and (max-width:200px){
    .transition-box{
      width:100%;
    }
  }
</style>



