    methods: {
      detailClose() {
        this.detailShow = false
      },
      changeGrids(data){
        this.map.removeLayer(this.Grid_1000M_GZ_random)
        this.map.removeLayer(this.GuangZhou_region_untiled)
        this.map.removeLayer(this.Pup_Grid_Hexagonal_500M_GZ)
        this.map.removeLayer(this.Pup_Grid_square_1km_GZ)
        this.map.addLayer(this.Grid_1000M_GZ_random)
        this.map.addLayer(this.GuangZhou_region_untiled)
        this.map.addLayer(this.Pup_Grid_Hexagonal_500M_GZ)
        this.map.addLayer(this.Pup_Grid_square_1km_GZ)
        this.GuangZhou_region_untiled.setVisible(false)
        this.Pup_Grid_Hexagonal_500M_GZ.setVisible(false)
        this.Grid_1000M_GZ_random.setVisible(false)
        this.Pup_Grid_square_1km_GZ.setVisible(false)
        for (let i in data) {
          switch (data[i]) {
            case "":
              this.GuangZhou_region_untiled.setVisible(true)
              break
            case "":
              this.Pup_Grid_Hexagonal_500M_GZ.setVisible(true)
              break
            case "1000M":
              this.Grid_1000M_GZ_random.setVisible(true)
              break
            case "1000M":
              this.Pup_Grid_square_1km_GZ.setVisible(true)
              break
          }
        }
        // this.GuangZhou_region_untiled.setVisible(this.GridDisplay[0].value)
        // this.Pup_Grid_Hexagonal_500M_GZ.setVisible(this.GridDisplay[1].value)
        // this.Grid_1000M_GZ_random.setVisible(this.GridDisplay[2].value)
        // this.Pup_Grid_square_1km_GZ.setVisible(this.GridDisplay[3].value)
      },
      changeMap(data){
        switch(data) {
          case '':
            this.radio_change(this.baidu_layer)
            break
          case '':
            this.radio_change(this.gaode_map)
            break
          case '':
            this.radio_change(this.tian_di_tu_road_layer)
            break
          case '':
            this.radio_change(this.google_map)
            break
          case '':
            this.radio_change(this.bing_map)
            break
        }
      },
      toggleLabelSelect() {
        this.showExcelSelect = false
        this.showSetting = false
        this.showLabelSelect = !this.showLabelSelect
        // GuangZhou_region_untiled.setVisible(this.showLabelSelect);
      },
