      },
      //CheckBox
      fun_onclick(check,layer_name){ 
        if(check){
          layer_name.setVisible(check);
        }       
        else{
          this.map.removeLayer(layer_name);
          layer_name.setVisible(check);
        }             
      },
      // sets the chosen WMS version
      setWMSVersion(wmsVersion) {
        this.map.getLayers().forEach(function(lyr) {
          lyr.getSource().updateParams({'VERSION': wmsVersion});
        });
        if(wmsVersion == "1.3.0") {
            var origin = this.bounds[1] + ',' + this.bounds[0];
        } else {
            var origin = this.bounds[0] + ',' + this.bounds[1];
        }
        tiled.getSource().updateParams({'tilesOrigin': origin});
      },  
      // Tiling mode, can be 'tiled' or 'untiled'
      setTileMode(tilingMode) {
        if (tilingMode == 'tiled') {
          untiled.set('visible', false);
          tiled.set('visible', true);
        } else {
          tiled.set('visible', false);
          untiled.set('visible', true);
        }
      },
      setAntialiasMode(mode) {
        this.map.getLayers().forEach(function(lyr) {
          lyr.getSource().updateParams({'FORMAT_OPTIONS': 'antialias:' + mode});
        });
      },
      setImageFormat(mime) {
        this.map.getLayers().forEach(function(lyr) {
          lyr.getSource().updateParams({'FORMAT': mime});
        });
      },
      setStyle(style){
        this.map.getLayers().forEach(function(lyr) {
          lyr.getSource().updateParams({'STYLES': style});
        });
      },
      setWidth(size){
        var mapDiv = document.getElementById('map');
        var wrapper = document.getElementById('wrapper');

        if (size == "auto") {
          // reset back to the default value
          mapDiv.style.width = null;
          wrapper.style.width = null;
        }
        else {
          mapDiv.style.width = size + "px";
          wrapper.style.width = size + "px";
        }
        // notify OL that we changed the size of the map div
        this.map.updateSize();
      },
      setHeight(size){
        var mapDiv = document.getElementById('map');
        if (size == "auto") {
          // reset back to the default value
          mapDiv.style.height = null;
        }
        else {
          mapDiv.style.height = size + "px";
        }
        // notify OL that we changed the size of the map div
        this.map.updateSize();
      },       
      updateFilter(visiable,layername){
        if (pureCoverage) {
          return;
        }
        var filterType = "cql";
        var region = document.getElementById('filterType').value;
        var square_Attribute=new Array();
        if(region==""){
          if(layername==this.Pup_Grid_Hexagonal_500M_GZ){
            var filter3 ="region in ('','','','','','','','','','','')";//
            square_Attribute=["popul_work","popul_livi","bank_count","hospital_c","school_cou"];
            //square_Attribute=["working_co","living_c_1","bank_cnt","hospital_c","school_cnt"];
          }
          else{
            var filter3 ="NAME in ('','','','','','','','','','','')";//
            square_Attribute=["working_co","living_c_1","bank_cnt","hospital_c1","school_cnt"];
            //square_Attribute=["popul_work","popul_livi","bank_count","hospital_c","school_cou"];
          }
        }
        else{
          if(layername==this.Pup_Grid_Hexagonal_500M_GZ){
            var filter3 ="region=" + "\'" + region + "\'";
            square_Attribute=["popul_work","popul_livi","bank_count","hospital_c","school_cou"];
            //square_Attribute=["working_co","living_c_1","bank_cnt","hospital_c","school_cnt"];
          }
          else{
            var filter3 ="NAME=" + "\'" + region + "\'";
            square_Attribute=["working_co","living_c_1","bank_cnt","hospital_c1","school_cnt"];
            //square_Attribute=["popul_work","popul_livi","bank_count","hospital_c","school_cou"];
          }
        } 

        // by default, reset all filters
        var filterParams = {
          'FILTER': null,
          'CQL_FILTER': null,
          'FEATUREID': null
        };
              
        if(document.getElementById(visiable).checked){        
          for (var i=0;i<square_Attribute.length;i++ ) {
            if (document.getElementById(square_Attribute[i]).value =="" & square_Attribute[i]!="hospital_c1") {
                continue;
            }
            else {                 
              if(square_Attribute[i]=="hospital_c1") {
                var compare=document.getElementById("hospital_c1_filterType").value;
                var cnt=document.getElementById("hospital_c1").value;
                if(cnt!="")
                  filter3+=" and "+" " + "hospital_c" + " "+" "+compare+" "+cnt;                    
              }
              else {
                var compare=document.getElementById(square_Attribute[i]+"_filterType").value;
                var cnt=document.getElementById(square_Attribute[i]).value;
                filter3+=" and "+" " + square_Attribute[i] + " "+" "+compare+" "+cnt;    
              }                 
            }
          }
          if (filter3.replace(/^\s\s*/, '').replace(/\s\s*$/, '') != "") {
            if (filterType == "cql") {
              filterParams["CQL_FILTER"] = filter3;
            }
          }
          layername.getSource().updateParams(filterParams);
          filter3=null;      
        }                          
      },
      resetFilter(visiable,layername) {
        if (pureCoverage) {
          return;
        }
        var square_Attribute=new Array();
        if(document.getElementById(visiable).checked){
          var filterParams = {
            'FILTER': null,
            'CQL_FILTER': null,
            'FEATUREID': null
          };
          filter="";
          filterParams["FILTER"] = filter;
          
          if(layername==this.Pup_Grid_Hexagonal_500M_GZ){
            square_Attribute=["popul_work","popul_livi","bank_count","hospital_c","school_cou"];
            //square_Attribute=["working_co","living_c_1","bank_cnt","hospital_c","school_cnt"];
          }
          else{
            square_Attribute=["working_co","living_c_1","bank_cnt","hospital_c1","school_cnt"];
            //square_Attribute=["popul_work","popul_livi","bank_count","hospital_c","school_cou"];
          }
          for (var i=0;i<square_Attribute.length;i++ ) {
            document.getElementById(square_Attribute[i]).value ="";
          }
          layername.getSource().updateParams(filterParams);
        }
      },
      // shows/hide the control panel
      toggleControlPanel(){
        var toolbar = document.getElementById("toolbar");
        if (toolbar.style.display == "none") {
          toolbar.style.display = "block";
        }
        else {
