      radio_change(map_Name){ 
        this.map.addLayer(this.tian_di_tu_road_layer);
        if(this.pre_choose==this.tian_di_tu_road_layer){//
          this.map.removeLayer(this.pre_choose);
          this.map.removeLayer(this.tian_di_tu_annotation);
        }
        else
          this.map.removeLayer(this.pre_choose);//       
        this.pre_choose=map_Name;//
        if(map_Name==this.tian_di_tu_road_layer){//
          this.map.addLayer(map_Name);
          this.map.addLayer(this.tian_di_tu_annotation);
        }
        else
          this.map.addLayer(map_Name);//   
      },
      getVETileUrl(z, x, y) {
        for(var a="",c=x,d=y,e=0;e<z;e++) {
          a=((c&1)+2*(d&1)).toString()+a;
          c>>=1;
          d>>=1
        }
        return 'http://dynamic.t0.tiles.ditu.live.com/comp/ch/' + a + '?it=G,VE,BX,L,LA&mkt=zh-cn,syr&n=z&og=111&ur=CN'
      },
      // Bingurl
      tileUrlFunction(coord, params1, params2) {

        return getVETileUrl(coord[0], coord[1], -coord[2] - 1);
      }
    },
    created:function() {
      // if (pureCoverage) {
      //   document.getElementById('filterType').disabled = true;
      //   document.getElementById('filter').disabled = true;
      //   document.getElementById('antialiasSelector').disabled = true;
      //   document.getElementById('updateFilterButton').disabled = true;
      //   document.getElementById('resetFilterButton').disabled = true;
      //   document.getElementById('jpeg').selected = true;
      //   format = "image/jpeg";
      // }

      // this.mousePositionControl = new ol.control.MousePosition({
      //   className: 'custom-mouse-position',
      //   target: document.getElementById('location'),
      //   coordinateFormat: ol.coordinate.createStringXY(5),
      //   undefinedHTML: '&nbsp;'
      // });

      // Grid_1000M_GZ_random.set('opacity',0.5);//50% 
      // map.addLayer(Grid_1000M_GZ_random);
      // Grid_1000M_GZ_random.setVisible(true);
    },
    mounted:function() {
      $('#map').click((evt)=>{
        if (this.showExcelSelect)
          this.toggleExcelSelect()
        if (this.showLabelSelect)
          this.toggleLabelSelect()
        if (this.showSetting)
          this.toggleSetting()
        this.detailShow = false
        setTimeout(()=>{
          let w = window.innerWidth
          let h = window.innerHeight
          let selfH = $('.detail')[0].scrollHeight
          let selfW = $('.detail')[0].scrollWidth
          if(w/2 < selfW || h/2 < selfH) {
            this.detailStyle.top =  evt.offsetY + 'px'
            this.detailStyle.left = evt.offsetX + 'px'
          } else {
            this.detailStyle.top =  (evt.offsetY > (h-selfH) ? evt.offsetY-selfH : evt.offsetY)+'px'
            this.detailStyle.left = (evt.offsetX > (w-selfW) ? evt.offsetX-selfW : evt.offsetX)+'px'
          }
          this.detailShow = true
        }, 280)
      })
      this.map.addLayer(this.tian_di_tu_road_layer)
      this.map.addLayer(this.tian_di_tu_annotation)
      this.pre_choose = this.tian_di_tu_road_layer

      this.map.addLayer(this.Grid_1000M_GZ_random)
      this.Grid_1000M_GZ_random.setVisible(false)
      this.map.addLayer(this.GuangZhou_region_untiled)
      this.GuangZhou_region_untiled.setVisible(false)
      this.map.addLayer(this.Pup_Grid_Hexagonal_500M_GZ)
      this.Pup_Grid_Hexagonal_500M_GZ.setVisible(false)
      this.map.addLayer(this.Pup_Grid_square_1km_GZ)
      this.Pup_Grid_square_1km_GZ.setVisible(false)

      this.map.getView().on('change:resolution', (evt) => {
        var resolution = evt.target.get('resolution')
        var units = this.map.getView().getProjection().getUnits()
        //var dpi = 25.4 / 0.28;
        var dpi = 25.4 / 1.12
        var mpu = ol.proj.METERS_PER_UNIT[units]
        var scale = resolution * mpu * 39.37 * dpi
        if (scale >= 9500 && scale <= 950000) {
          scale = Math.round(scale / 1000) + "K"
        } else if (scale >= 950000) {
          scale = Math.round(scale / 1000000) + "M"
        } else {
          scale = Math.round(scale)
        }
        document.getElementById('scale').innerHTML = "Scale = 1 : " + scale
      });

      this.map.getView().fit(this.bounds, this.map.getSize())

      //
      this.map.on('singleclick', (evt) => {
        // if (this.showExcelSelect)
        //   this.toggleExcelSelect()
        // else if (this.showLabelSelect)
        //   this.toggleLabelSelect()
        // else {
        //   this.detailShow = false
        //   setTimeout(()=>{
        //     let w = window.innerWidth
        //     let h = window.innerHeight
        //     let selfH = $('.detail')[0].scrollHeight
        //     let selfW = $('.detail')[0].scrollWidth
        //     this.detailStyle.top =  (evt.pixel[1] > (h-selfH) ? evt.pixel[1]-selfH : evt.pixel[1])+'px'
        //     this.detailStyle.left = (evt.pixel[0] > (w-selfW) ? evt.pixel[0]-selfW : evt.pixel[0])+'px'
        //     this.detailShow = true
        //   }, 280)
        // }

        var view = this.map.getView()
        var viewResolution = view.getResolution()
        if(this.GuangZhou_region_untiled.get('visible')){
          var source = this.GuangZhou_region_untiled.get('visible')
            ? this.GuangZhou_region_untiled.getSource() 
            : this.GuangZhou_region_tiled.getSource()
          var url = source.getGetFeatureInfoUrl(
          evt.coordinate, viewResolution, view.getProjection(),
            {'INFO_FORMAT': 'text/html', 'FEATURE_COUNT': 50})
          if (url)
            document.getElementById('nodelist').innerHTML = '<iframe seamless src="' + url + '"></iframe>'
        }
        else if(this.Pup_Grid_Hexagonal_500M_GZ.get('visible')){
          var source = this.Pup_Grid_Hexagonal_500M_GZ.get('visible')
            ? this.Pup_Grid_Hexagonal_500M_GZ.getSource()
            : this.Pup_Grid_Hexagonal_500M_GZ_tiled.getSource()
          var url = source.getGetFeatureInfoUrl(
            evt.coordinate, viewResolution, view.getProjection(),
            {'INFO_FORMAT': 'text/html', 'FEATURE_COUNT': 50})
          if (url)
            document.getElementById('nodelist').innerHTML = '<iframe seamless src="' + url + '"></iframe>'
        }
        else if(this.Pup_Grid_square_1km_GZ.get('visible')){
          var source = this.Pup_Grid_square_1km_GZ.get('visible')
            ? this.Pup_Grid_square_1km_GZ.getSource()
            : this.Pup_Grid_square_1km_GZ_tiled.getSource()
          var url = source.getGetFeatureInfoUrl(
            evt.coordinate, viewResolution, view.getProjection(),
            {'INFO_FORMAT': 'text/html', 'FEATURE_COUNT': 50})
          if (url)
              document.getElementById('nodelist').innerHTML = '<iframe seamless src="' + url + '"></iframe>'
        }                    
      })

      this.map.on('pointerdrag', (evt) => {
        if(this.detailShow)
          this.detailShow = false
      })
    },
    components: {
      LabelLoc,
      Detail,
      Export,
      ExcelSelect,
      Setting}
  }
</script>



<style lang="scss">
  // @media screen and (min-width: 320px) {
  //     html {font-size: 14px;}
  // }
   
  // @media screen and (min-width: 360px) {
  //     html {font-size: 16px;}
  // }
   
  // @media screen and (min-width: 400px) {
  //     html {font-size: 18px;}
  // }
   
  // @media screen and (min-width: 440px) {
  //     html {font-size: 20px;}
  // }
   
  // @media screen and (min-width: 480px) {
  //     html {font-size: 22px;}
  // }
   
  // @media screen and (min-width: 640px) {
  //     html {font-size: 28px;}
  // }
  html, body, #app{
    margin:0;
    padding:0;
    width:100%;
    height:100%;
    font-size:20px;
  }
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
    pointer-events:none;
    position:absolute;
    top:0;
    left:0;
    z-index:10;
    display: flex;
    .button-bar{
      display: flex;
      flex-direction:column;
      flex-wrap: wrap;
      height:50%;
      width:120px;
      .gs-button {
        margin:10px 0 0 10px;
        width:120px;
        height:35px;
        min-height:35px;
        font-size:0.5rem;
        pointer-events:auto;
      }
      .gs-button:nth-child(1){
        margin-top:75%;
      }
    }
    .gs-elCard{
      // opacity:0.8;
      pointer-events:auto;
    }
    .detail{
      width:220px;
    }
  }
  @media screen and (max-width:500px){
    #app{
      flex-direction:column;
      justify-content: flex-end;
      .button-bar{
        align-self:flex-end;
        justify-content: flex-end;
        flex-wrap: wrap-reverse;
        .gs-button {
          margin:0 5% 5% 0;
        }
        .gs-button:nth-child(1){
          margin-top:0;
        }
      }
      .detail{
        width:170px;
      }
    }
  }
</style>



